.item{
    width: 350px;
    border: 3px solid black;
    border-radius: 35px;
}
.item img{
    border-radius: 35px;
}
.item p{
    margin-left: 15px;
}
.item-prices{
    display: flex;
    gap: 20px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
    margin: 5px 30px;
}
.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
    margin: 5px;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}